/*============================================================================
	#Header
==============================================================================*/
.header {
	

	&__top {
		padding: 8px 0 10px;
		background: #000;

		.row {
			@include row-flex();
			align-items: center;
		}
	}

	&__nav {
		background: #161616;

		.row {
			@include row-flex();
		}
	}

	&__logo {
		margin-right: auto;
		margin-left: 4px;
	}

	&__info {
		display: flex;
		align-items: center;
	}

	&__worktime {
		margin-right: 32px;
	}

	&__address,
	&__phone {
		position: relative;
		@include text-descr(white);
		height: 100%;
		margin-right: 40px;
		text-decoration: none;
		transition: color 0.3s ease;

		&::after {
			content: '';
			position: absolute;
			height: 1px;
			width: 100%;
			background: white;
			bottom: 2px;
			left: 0;
			transition: background 0.3s ease;

		}

	}

	&__phone {

		&:hover {
			color: $colorMain;

			&::after {
				background: $colorMain;
			}
		}
	}


	&__controll {
		display: flex;
		align-items: center;

	.button {
		fill: $colorDscr;
		color: $colorDscr;
		
		&:not(:last-child){
			margin-right: 20px;
		}

		&:hover {
			color: $colorMain;
			fill: $colorMain;
		} 
	}
	}

	&__search {
		font-size: 14px;

		.icon {
			margin-left: 10px;
		}
	}

}

.worktime {
	display: flex;
	@include text-descr();

	&__item{

		&:not(:last-child){
			position: relative;
			margin-right: 7px;
			padding-right: 8px;

			
			&:after {
				content: '';
				position: absolute;
				top: 50%;
				right: 0;
				height: 11px;
				width: 1px;
				background: white;
				transform: translateY(-65%);
			}
		}
	}

	&__time {
		color: white;
	}
}

.menu {
	position: relative;

	&__list {
		padding: 0;
		margin: 0;
		list-style-type: none;
		display: flex;
	}

	&__item {

		&._has-submenu {
			// position: relative;
		}

		&:not(:last-child) {
			margin-right: 40px;
		}
		
	}

	&__link {
		color: white;
		text-decoration: none;
		display: block;
		padding: 16px 0;
		transition: color 0.3s ease;

		&._is-active {
			position: relative;

			&::after {
				content: '';
				position: absolute;
				height: 4px;
				width: 100%;
				background: $colorMain;
				left: 0;
				bottom: 0;
			}
		}

		&:hover {
			color: $colorMain;
		}
	}
	
}

.submenu {
	position: absolute;
	background: white;
	width: 365px;
	margin-right: 12px;
	// padding: 22px 26px 34px 32px;
	padding: 22px 0px 34px 0px;
	border-radius: 8px;
	margin-top: 4px;
	left: -20px;

	opacity: 0;
	visibility: hidden;
	pointer-events: none;
	transform: translateX(30px);
	transition: all 0.3s ease;

	&__header {
		display: flex;
		align-items: center;
		justify-content: space-between;
		min-height: 40px;
		margin-bottom: 40px;
		padding: 0px 26px 0px 32px;
		

		.button {
			color: $colorDark;
			fill: $colorDark;
			height: 100%;
			margin-right: 0 !important;
		}
	}

	&__title {
		align-self: flex-start;
	}

	&__list {
		margin: 0;
		padding: 0;
		list-style-type: none;
		
	}

	&__item {
		display: flex;
		justify-content: space-between;
		padding: 0px 26px 0px 32px;
		

		&:not(:last-child) {
			margin-bottom: 30px;
		}
		
		&:hover {
			fill: $colorMain;

			> .submenu__link {
				color: $colorMain;
			}
		}
	}

	&__link {
		color: $colorDark;
		text-decoration: none;
		font-size: 20px;
		transition: color 0.3s ease;
		display: flex;
		justify-content: space-between;
		width: 100%;
		
	}

	&__link-text {
		white-space: nowrap;
		
		overflow: hidden;
		text-overflow: ellipsis;
	}
	
}

.submenu._is-visible {
	transform: translateX(0);
	opacity: 1;
	visibility: visible;
	pointer-events: all;
}

.submenu .submenu {
	margin-top: 0;
	background: white;
	left: calc(100% + 12px);
	transform: translateY(-22px) translateX(30px);
	transition: all 0.3s ease;

	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: -12px;
		height: 100%;
		width: 12px;
		background: transparent;
	} 

	.submenu__header {
		font-size: 20px;
		color: $colorDscr;
		align-items: flex-start;
		justify-content: flex-start;
		margin-top: 2px
	}

	.submenu__title {
		padding: 1px 0 0;
		margin-left: 12px;
	}
}

.submenu__item._has-submenu:hover .submenu {
	visibility: visible;
	opacity: 1;
	pointer-events: all;
	transform: translateY(-22px) translateX(0px);
}

