/*============================================================================
	COMMON
==============================================================================*/
.container {
	@include wrapper();
}

.row {
	@include row-flex();
}

@media (max-width: 1440px) {

	.container {
		max-width: 1160px;
		padding: 0 20px;
	}

}

@media (max-width: 1210px) {

	.container {
		max-width: 1160px;
		padding: 0 10px;
	}

}

.body {

	&::before {
		content: '';
		position: fixed;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
		background: #000;
		opacity: 0;
		visibility: hidden;
		pointer-events: none;
		z-index: 100;
		transition: all 0.3s ease;
	}


	&.overlay {
		position: relative;

		&::before {
			opacity: 0.6;
			visibility: visible;
			pointer-events: all;
		}

		

		.header .submenu{
			z-index: 101;
		}
	}
}

.quick-nav {
	position: fixed;
	top: 50%;
	right: 50px;
	z-index: 10;
	transform: translateX(100%) translateY(-50%);


	&__item {
		background: $colorMain;
		margin-bottom: 1px;
	}

	.button-primary {
		padding-right: 20px;
		transition: transform 0.3s ease;

		.icon {
			margin-right: 16px;
		}

		&:hover {
			transform: translateX(calc(-100% + 50px));
			background: inherit;
		} 
	}
}