/*============================================================================
	#Button
==============================================================================*/
.button {
	padding: 0;
	margin: 0;
	background: none;
	border: none;
	box-shadow: none;
	cursor: pointer;
	transition: color 0.3s ease;

	&._icon {
		display: flex;
		align-items: center;
	}

	&._wishlist {
		position: relative;

		&::before {
			content: '';
			position: absolute;
			top: 0;
			right: 0;
			width: 12px;
			height: 12px;
			background: $colorMain;
			border: 2px solid #000;
			border-radius: 100%;
		}
	}
}


.button-primary {
	font-size: 16px;
	line-height: 1.52;
	color: white;
	display: flex;
	height: 100%;
	align-items: center;
	justify-content: center;
	background: $colorMain;
	padding: 8px 10px;
	text-decoration: none;
	border: none;
	cursor: pointer;
	transition: background 0.3s ease,
				color 0.3s ease;

	fill: white;

	&._icon-right {
		.icon {
			margin-left: 8px;
		}
	}

	&._icon-left{
		.icon {
			margin-right: 8px;
		}
	}

	&:hover {
		background: $colorMainHover;
	}

	&:disabled {
		opacity: 0.4;
		cursor: inherit;
		background: $colorMain;

		.icon {
			cursor: inherit;
		}
	}
}

.button-primary._dark {
	background: $colorDarkGray;


	&:hover {
		background: $colorDarkGrayHover;
		color: $colorMain;
		fill: $colorMain;
	}

	&:disabled {
		color: $colorDscr;
		fill: $colorDscr;;
		background: $colorDarkGray;
		opacity: 1;
		
	}
}

.button-totop {
	color: white;
	fill: white;
	text-decoration: none;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 120px;
	height: 40px;
	background: $colorDarkGray;
	transition: color 0.3s ease;


	.icon {
		margin-left: 10px;
	}

	&:hover {
		color: $colorMain;
		fill: $colorMain;
	}

}
