/*============================================================================
	#Home Page
==============================================================================*/
.main-slider {
	position: relative;
	max-height: 700px;
	overflow: hidden;
	background: #161616;

	&__slide {
		height: 700px;
		padding: 83px 0 120px;
		box-sizing: border-box;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}

	&__media {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__content {
		position: relative;
		z-index: 1;
	}

	&__title {
		@include H1(white);
		line-height: 1.185;
		text-align: center;
		max-width: 690px;
		margin: 0 auto;
		text-shadow: 0px 4px 15px rgba(0, 0, 0, 0.35);
	}

	&__footer {
		position: relative;
		z-index: 1;

		.row {
			justify-content: center;
		}

		.button-primary {
			margin: 0 13px;
			padding-left: 10px;
			padding-right: 10px;
			min-width: 222px;
			height: 50px;
		}
	}

	&__pagination {
		position: absolute;
		z-index: 1;
		bottom: 23px !important;
		display: flex;
		justify-content: center;

		span {
			width: 50px;
			border-radius: 0;
			height: 4px;
			display: inline-block;
			background: #D7D7D7;
			opacity: 1;

			&.swiper-pagination-bullet-active {
				background: $colorMain;
			}

			&:hover {
				background: $colorMainHover;
			}
		}
	} 


}