/*============================================================================
	#Icons
==============================================================================*/

.icon {

	height: 30px;
	width: 30px;
	display: flex;
	justify-content: center;
	align-items: center;
	transition: fill 0.3s ease;
	flex: 0 0 auto;
	cursor: pointer;

	// svg {
	// 	width: 20px;
	// 	height: 20px;
	// }

	&._xl {
		height: 70px;
		width: 70px;
	}

	&._top {
		transform: rotate(-90deg);
	}
	

	&-search {
		width: 20px;
		height: 20px;
	}

	&-heart {
		width: 20px;
		height: 18px;
	}

	&-user {
		width: 18px;
		height: 20px;
	}

	&-times {
		width: 15px;
		height: 15px;
	}

	&-arrow-next,
	&-arrow-prev {
		height: 16px;
		width: 10px;
	}

	&-phone {
		width: 20px;
		height: 20px;
	}

	&-phone {
		width: 20px;
		height: 20px;

		&._xl {
			height: 47px;
			width: 47px;
		}
	} 

	&-test-drive {
		width: 20px;
		height: 20px;

		&._xl {
			height: 47px;
			width: 47px;
		}
	}

	&-book {
		height: 20px;
		width: 17px;

		&._xl {
			height: 47px;
			width: 39px;
		}
	}

	&-checkmark {
		width: 20px;
		height: 20px;

		&._xl {
			height: 47px;
			width: 47px;
		}
	}

	&-sale {
		width: 20px;
		height: 20px;

		&._xl {
			height: 47px;
			width: 47px;
		}
	} 

	&-facebook {
		width: 12px;
		height: 22px;
	}

	&-instagram {
		width: 22px;
		height: 22px;
	}

	&-youtube {
		width: 25px;
		height: 19px;
	} 

}