/*============================================================================
	#Footer
==============================================================================*/
.footer {
	
	&__nav {
		background: $colorDark;
	}

	&__main {
		background: #2D2D2D;
	}

}


.footer-nav {
	padding: 50px 0 70px;

	.row {
		justify-content: space-between;
	}


	&__item {
		display: block;
		color: white;
		fill: white;
		text-decoration: none;
		@include H3(white);
		background: $colorDarkGray;
		padding: 30px 20px 20px 30px;
		min-height: 250px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		width: calc((100% - 100px ) / 5);
		transition: background 0.3s ease;

		&:hover {
			background: $colorMain;
		}
	}

	&__icon {
		height: 100px;
		width: 100px;
		border-radius: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		background: $colorDark;
	}

	&__text {
		display: flex;
		justify-content: space-between;
		align-items: flex-end;
	}
}

.footer-main {

	&__top {
		padding: 30px 0;

		.row {
			align-items: center;
			justify-content: space-between;
		}
	}

	&__breadcrumbs {
		color: $colorGray;
		fill: $colorGray;
	}

	&__body {
	

		.row {
			padding-top: 30px;
			padding-bottom: 30px;
			border-top: 1px solid #616161;
			border-bottom: 1px solid #616161;
			justify-content: space-between;
		}
	}

	&__subscribe {
		flex: 0 0 630px;
		max-width: 630px;
	}
}

.subscribe {

	&__title {
		color: white;
		margin-bottom: 13px;
	}

	&__descr {
		@include Header18(white)
		margin: 0;
	}

	&__form {
		margin-top: 23px;
		display: flex;
		align-items: flex-end;

		.button-primary {
			margin-left: 20px;
			width: 144px;
			height: 40px;
			flex: 0 0 auto;
		}

		.label {
			color: white;
		}

	}
}

.social {
	color: white;
	fill: white;

	&__title {
		color: white;
		margin-bottom: 24px;
	}

	&__list {
		display: flex;
	}

	&__item {
		width: 50px;
		height: 50px;
		background: $colorDarkGray;
		display: flex;
		justify-content: center;
		align-items: center;
		transition: background 0.3s ease;
		fill: white;
		
		&:not(:last-child){
			margin-right: 10px;
		}

		&:hover {
			background: $colorMain;

			fill: $colorDarkGray;
		}
	}
}